<template>
  <div class="work-report-page">
    <h1>{{ displayedUserName }} </h1>
    <div class="work-report-container">
      <h2></h2>
      <table>
        <thead>
          <tr>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(hours, month) in monthlyWorkHours" :key="month">
            <td>{{ month }}</td>
            <td>{{ hours }} </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  name: 'WorkReportPage',
  data() {
    return {
      username: '',
      monthlyWorkHours: {}, //   
    };
  },
  computed: {
    displayedUserName() {
      const nameMap = {
        'info@dxpro-sol.com': '',
        'kim_taehoon@dxpro-sol.com': ' ',
        'otomo_kento@dxpro-sol.com': ' ',
        'choi_kyungjun@dxpro-sol.com': ' ',
      };
      return nameMap[this.username] || this.username; //  
    },
  },
  async mounted() {
    console.log("Component Mounted");
    this.username = sessionStorage.getItem('username'); // sessionStorage username 
    if (this.username) {
      console.log("Username:", this.username);
      await this.fetchWorkHours(); // workReports  
    } else {
      console.log("Username not found in sessionStorage");
    }
  },
  methods: {
    async fetchWorkHours() {
      try {
        const response = await fetch('https://dxpro.onrender.com/workReports');
        if (!response.ok) throw new Error(`HTTP error! status: ${response.status}`);
        const data = await response.json();
        console.log(response);
        //   
        const groupedWorkHours = this.aggregateByMonth(data);
        
        //   
        console.log("Grouped Work Hours:", groupedWorkHours);
        this.monthlyWorkHours = groupedWorkHours;
      } catch (error) {
        console.error('Error fetching work hours:', error);
      }
    },
    aggregateByMonth(data) {
      const grouped = {};
      data.forEach((record) => {
        const month = record.date.slice(0, 6); // YYYYMM  
        if (!grouped[month]) grouped[month] = 0;
        grouped[month] += record.workHours; //   
      });
      return grouped;
    },
  },
};
</script>

